.personal-cabinet-login {
  &__back {
    display: none;
    @media screen and (min-width: 1250px){
      position: absolute;
      left: 74px;
      top: 48px;
      z-index: 1;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      gap: 16px;
      color: #FFFFFF;
      .back {
        &-icon {
          background: rgba($color: #FFFFFF, $alpha: 0.1);
          backdrop-filter: blur(7px);
          border-radius: 50%;
          width: 38px;
          height: 38px;
          display: flex;
          align-items: center;
          justify-content: center;
        }
      }
    }
  }
  &__main {
    &__content {
      display: flex;
      flex-direction: row;
      .content {
        &-preview {
          @media screen and (max-width: 1250px){
            display: none;
          }
          width: 71.5625%;
          display: flex;
          flex-direction: column;
          justify-content: center;
          background: linear-gradient(183.29deg, #080C2C 13.99%, #110036 93.14%);
          &__title {
            margin-bottom: 34px;
            margin-left: 21.834%;
            font-weight: 800;
            font-size: 32px;
            line-height: 41px;
            color: #FFFFFF;
          }
          &__slider {
            user-select: none;
            .slider-indicators.archSlide > * {
              width: 70px;
              height: 7px;
              background: #FFFFFF;
              border-radius: 2px;
              margin: 0 4px;
            }
            .slider-container {
              &__item {
                .item {
                  // margin-left: 21.834%;
                  width: 772px;
                  margin: 0 auto;
                  position: relative;
                  &-img {
                    position: relative;
                    background: #FFE2EC;
                    box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.06);
                    border-radius: 6px;
                    width: 100%;
                    &__svg {
                      position: absolute;
                      bottom: -7px;
                      right: -6px;
                      z-index: 2;
                    }
                    &__bottom {
                      position: absolute;
                      bottom: 0;
                      left: 0;
                      width: 100%;
                      height: 89px;
                      background: #FDC3D7;
                      border-radius: 0px 0px 12px 12px;
                    }
                  }
                  &-content {
                    position: absolute;
                    margin: 0;
                    &__title {
                      margin: 0;
                      margin-top: 26px;
                      margin-left: 32px;
                      font-weight: 800;
                      font-size: 24px;
                      line-height: 31px;
                      color: #1D1D1D;
                    }
                  }
                }
              }
            }
          }
        }
        &-auth {
          @media screen and (max-width: 1250px){
            width: 100%;
          }
          width: 28.4375%;
          padding: 74px 0;
          position: relative;
          display: flex;
          flex-direction: column;
          justify-content: flex-start;
          align-items: center;
          /* @media screen and (min-width: 480px){
            padding: 74px 16px;
          } */
          overflow: hidden;
          &__exit-button {
            position: absolute;
            right: 16px;
            top: 74px;
            background: rgba(12, 49, 76, 0.1);
            display: flex;
            justify-content: center;
            align-items: center;
            height: 24px;
            width: 24px;
            border-radius: 50%;
            transition: all 0.5s;
            transform: translateX(0);
          }
          &__back-button {
            position: absolute;
            left: 16px;
            top: 74px;
            background: rgba(#FFFFFF, .7);
            display: flex;
            justify-content: center;
            align-items: center;
            height: 24px;
            width: 24px;
            border-radius: 50%;
            transition: all 0.5s;
            transform: translateX(-50px);
          }
          &.submit-sms {
            .content-auth {
              &__container {
                transform: translateX(-25%);
              }
              &__exit-button {
                transform: translateX(50px);
              }
              &__back-button {
                transform: translateX(0px);
              }
            }
          }
          &__logo {
            display: flex;
            flex-direction: row;
            justify-content: center;
            align-items: center;
            gap: 11px;
            .logo {
              &-icon {

              }
              &-text {
                &__title {
                  margin-bottom: 1.5px;
                  font-weight: 800;
                  font-size: 24px;
                  line-height: 31px;
                  color: #1D1D1D;
                }
                &__discription {
                  font-weight: 500;
                  font-size: 16px;
                  line-height: 20px;
                  color: #0C314C;
                }
              }
            }
          }
          .underline {
            width: 100%;
            height: 1px;
            background: #1d1d1d;
            margin: 39px 0;
            max-width: 402px;
            opacity: 0;
            @media screen and (min-width: 1250px){
              opacity: .2;
              margin: 65px 0;
            }
          }
          &__title {
            font-weight: 800;
            font-size: 28px;
            line-height: 36px;
            color: #1D1D1D;
          }
          &__submit-button {
            padding: 14px;
            width: 100%;
            max-width: 402px;
            margin-top: 16px;
            user-select: none;
          }
          &__phone {
            width: 100%;
            margin-top: 10px;
            display: flex;
            flex-direction: column;
            align-items: center;
            // justify-content: space-between;
            padding: 0 16px;
            height: 204px;
            .phone {
              &-text {
                font-weight: 500;
                font-size: 14px;
                line-height: 150%;
                text-align: center;
                color: #1D1D1D;
                opacity: 0.7;
                width: 100%;
                max-width: 250px;
                margin-bottom: 48px;
              }
            }
            input {
              font-weight: 500;
              font-size: 16px;
              line-height: 20px;
              padding: 14px;
              padding-left: 74px;
              width: 100%;
              max-width: 402px;
            }
          }
          &__container {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            width: 200%;
            transition: all .5s;
            transform: translateX(25%);
            // &.submit-sms {
            //   transform: translateX(-25%);
            // }
          }
          &__numbers {
            width: 100%;
            padding: 0 16px;
            margin-top: 10px;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-between;
            height: 204px;
            .phone {
              &-text {
                font-weight: 500;
                font-size: 14px;
                line-height: 150%;
                text-align: center;
                color: #1D1D1D;
                opacity: 0.7;
                width: 100%;
                max-width: 250px;
                margin-bottom: 28px;
              }
            }
            input {
              font-weight: 500;
              font-size: 16px;
              line-height: 20px;
              padding: 14px;
              // padding-left: 74px;
              width: 20%;
              text-align: center;
              // max-width: 402px;
            }
            .content-area {
              width: 100%;
              max-width: 402px;
            }
            .number-code {
              display: flex;
              flex-direction: row;
              align-items: center;
              justify-content: space-between;
              width: 100%;
              flex-wrap: wrap;
              margin-bottom: 18px;
            }

            button.content-auth__submit-button__numbers {
              width: 100%;
              padding: 14px;
            }


          }
        }
      }
    }
  }

}
